#navbar-header{
  width: 100%;
  max-width: 75rem;
  margin: auto;
  @include transitionEffect(350ms);
  //transform: translateX(-150%);
  z-index: 402;
  position: relative;
  top: 0;
  height: auto;
  @include flexDisplay();
  //opacity: 0;
  //overflow: auto;
  .navigation-left-region, .navigation-region{
    //padding: rem-calc(72px 116px);
    nav > ul{display: block;}
  }
  .navigation-region {
    &:before {
      content: "";
      width: 100vw;
      position: absolute;
      border-top: 1px solid #cfd6cf;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
    }
    ul{
      li{
        &.is-dropdown-submenu-parent {
          position: static;
        }
        &.is-active {
          a {
            background-color: $light-blue;
          }
        }
        a{
          font-family: $font-main;
          font-size: rem-calc(18px);
          line-height: 1.2;
          @include setFont($fontType-medium);
        }
        /*
        &:not(:last-of-type){
          margin-bottom: rem-calc(8px);
        }
        */

        ul {
          &.menu { // Hide submenus before classes (submenu is-dropdown-submenu first-sub vertical) are applied to them
            display: none;
          }
          &.submenu.is-dropdown-submenu {
            background-color: $light-blue;
            width: 100%;
            left: 0;
            flex-direction: row;
            justify-content: center;
            flex-wrap: wrap;
            padding: 1rem 0;
            &:before {
              content: "";
              position: absolute;
              z-index: 0;
              left: 50%;
              top: 0;
              bottom: 0;
              width: 100vw;
              transform: translateX(-50%);
              background-color: $light-blue;
            }
            &.js-dropdown-active {
              //display: flex;
              display: grid;
              grid-template-columns: 33.333333% 33.333333% 33.333333%;
            }
            li {
              position: relative;
              //width: auto;
              //min-width: 16.6666667%;
              //max-width: 25%;
              padding: 0 0.5rem;
              z-index: 1;
              display: flex;
              //text-align: center;
              //align-items: center;
              .acc-link {
                width: 100%;
                padding: 0 1rem;
                a {
                  display: block;
                  position: relative;
                  height: 100%;
                  padding: 0.7rem 0;
                  transition: all 0.3s ease-in-out;
                  &:after {
                    content: "";
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    margin-top: 0;
                    border-top: 1px solid $gray2;
                  }
                  &:focus,
                  &:hover {
                    padding: 0.7rem 0.5rem;
                    background-color: lighten($light-blue, 3%);
                  } 
                }
              }
              &:nth-child(-n+3) {
                .acc-link {
                  a {
                    &:after {
                      display: none;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
