// NEWS, EVENTS AND AGENDA FILTERS
.news-filters-form-container,
.home-agenda .bef-exposed-form,
.event-filters-form-container {
  width: calc(100% - 180px);
  margin: 0;
  .bef-links {
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        margin-right: 0.5rem;
        margin-bottom: 0.5rem;
        a {
          font-family: $font-main;
          text-align: center;
          font-size: rem-calc(13px);
          line-height: 1.2;
          @include setFont($fontType-medium);
          padding: rem-calc(4px 12px);
          display: inline-block;
          border-radius: 50rem;
          border: 1px solid $gray2;
          &:hover,
          &:focus {
            text-decoration: none;
            color: $gold-p1ps;
            border-color: $gold-p1ps;
          }
          &.bef-link--selected {
            text-decoration: none;
            color: $white;
            background-color: $gold-p1ps;
            border-color: $gold-p1ps;
          }
        }
      }
    }
  }

  @include breakpoint($large - 1 down) {
    width: calc(100% - 0.75rem);
    margin-left: 0.75rem;
    .views-exposed-form{
      .form-item:not(.form-actions){
        width: 100%;
      }
    }
    .bef-links {
      //width: calc(100% - 1rem);
      //margin-left: 1rem;
      width: 100%;
      overflow: auto;
      ul {
        flex-wrap: nowrap;
        li {
          a {
            word-break: normal;
            white-space: nowrap;
          }
        }
      }
    }
  }
  .form-actions {
    display: none;
  }
}

// EVENTS
.bootstrap-cards {
  &.event {
    height: 100%;
    display: flex;
    flex-direction: column;
    text-align: left;
    .img-container {
      flex-grow: 0;
      margin-bottom: rem-calc(12px);
      .categ-wrapper {
        position: absolute;
        top: 1rem;
        right: 1rem;
      }
    }
    .same-height {
      flex-grow: 1;
      justify-content: flex-start;
      position: relative;
      padding-bottom: rem-calc(32px);
    }
    .date-wrapper {
      margin-bottom: rem-calc(8px);
      display: flex;
      padding: 3px 0;
      &:before {
        content: "";
        width: 22px;
        height: 24px;
        display: block;
        background-image: url(../images/p1ps2022/picto-calendar.svg);
        background-repeat: no-repeat;
        background-size: 100% auto;
        flex-grow: 0;
        margin-right: rem-calc(8px);
      }
      .date-style {
        line-height: 1.25;
        color: $text-main-color;
        padding: 3px 0;
        > * {
          display: inline;
          margin-right: rem-calc(8px);
        }
      }
    }
    .bottom-infos {
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }
}
// NEWS
.news {
  &.news-teaser {
    //height: 100%;
    //position: relative;
    //padding-bottom: 2.5rem;
    .img-container {
      margin-bottom: rem-calc(12px);
      .categ-wrapper {
        position: absolute;
        top: 1rem;
        right: 1rem;
      }
    }
    .text-wrapper {
      p {
        margin-bottom: 1rem;
        width:100%;
        /**Major Properties**/
        line-height: 1.6;
        max-height: 4.8rem;
        -webkit-box-orient: vertical;
        display: block;
        display: -webkit-box;
        overflow: hidden !important;
        text-overflow: ellipsis;
        -webkit-line-clamp: 3;
        word-break: break-word;
      }
    }
    .date-wrapper {
      display: flex;
      //position: absolute;
      //bottom: 0;
      &:before {
        content: "";
        width: 22px;
        height: 24px;
        display: block;
        background-image: url(../images/p1ps2022/picto-calendar.svg);
        background-repeat: no-repeat;
        background-size: 100% auto;
        flex-grow: 0;
        margin-right: rem-calc(8px);
      }
      .date-style {
        line-height: 1.25;
        color: $text-main-color;
        padding: 3px 0;
        > * {
          display: inline;
          margin-right: rem-calc(8px);
        }
      }
    }
    h2 {
      margin-bottom: rem-calc(24px);
      &.h4-style {
        font-size: rem-calc(24px);
      }
    }
  }
}